@charset "UTF-8";
[data-lazy] {
  transition: opacity 0.25s ease-in;
  min-height: 120px;
  width: 100%;
  opacity: 0; }

.loaded {
  @apply opacity-100; }

.inner {
  width: 100%;
  max-width: 72rem;
  margin: 0 auto;
  padding: 1.5rem; }

body {
  margin: 0; }

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0; }

.not-sr-only {
  position: static;
  width: auto;
  height: auto;
  padding: 0;
  margin: 0;
  overflow: visible;
  clip: auto;
  white-space: normal; }

a {
  color: #ebf8ff;
  text-decoration: none;
  transition-property: all 300ms ease-out; }
  a:not([class]) {
    color: #2c5282;
    border-bottom-width: 2px; }
    a:not([class]):hover {
      color: #9f7aea; }

h1,
h2,
h3 {
  font-weight: 700;
  font-family: "Philosopher", sans-serif;
  line-height: 1.25;
  margin-top: 0;
  margin-bottom: 1.5rem;
  color: #21183c; }

.page-title {
  font-size: 3rem; }

.content p {
  margin: 2rem; }

blockquote {
  display: block;
  font-weight: 700;
  font-family: "Philosopher", sans-serif;
  font-size: 2rem;
  position: relative;
  padding-bottom: 2.5rem;
  margin: auto;
  width: 66.66667%; }
  blockquote:before {
    position: absolute;
    top: 0;
    line-height: 0;
    font-size: 15rem;
    content: "\00AB";
    color: rgba(0, 0, 0, 0.1); }
  blockquote:after {
    border: 2px solid #2b6cb0;
    content: "";
    bottom: 1.5rem;
    left: 50%;
    margin-left: -100px;
    position: absolute;
    height: 3px;
    width: 200px; }
  blockquote cite {
    display: block;
    font-size: 1.25rem; }
    blockquote cite:before {
      content: "– "; }

.header {
  position: relative;
  padding: 1rem;
  align-items: center;
  display: block;
  background-color: #ebf8ff; }
  @media (min-width: 768px) {
    .header {
      padding: 1.5rem; } }
